@import url(https://fonts.googleapis.com/css?family=Roboto);
html {
	background: #fff;
	color: #000;
	min-height: 100%;
}

html, body, #root {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	font-family: Roboto, sans-serif;
	font-size: 13px;
	margin: 0;
	padding: 0;
}

body {
	min-height: 100vh;
}

.ReactModal__FitOverlay {
	align-items: flex-start;
	background: rgba(0, 0, 0, 0.6);
	display: flex;
	justify-content: center;
	opacity: 0;
	padding-top: 50px;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	-webkit-transition: 0.10s opacity;
	transition: 0.10s opacity;
}

.ReactModal__FitContent {
	background: #fff;
	border: 1px solid #ccc;
	border-radius: 4px;
	max-height: 90vh;
	max-width: 90vw;
	opacity: 0;
	outline: none;
	overflow: auto;
	-webkit-overflow-scrolling: touch;
	padding: 16px;
	position: static;
	-webkit-transform: translateY(-10px) perspective(600px) rotateX(12deg) scale(0.95);
	        transform: translateY(-10px) perspective(600px) rotateX(12deg) scale(0.95);
	-webkit-transition: 0.10s opacity, 0.15s -webkit-transform;
	transition: 0.10s opacity, 0.15s -webkit-transform;
	transition: 0.15s transform, 0.10s opacity;
	transition: 0.15s transform, 0.10s opacity, 0.15s -webkit-transform;
}

.ReactModal__Overlay--after-open {
	opacity: 1;
	-webkit-transition: 0.15s opacity;
	transition: 0.15s opacity;
}

.ReactModal__Overlay--before-close {
	opacity: 0;
}

.ReactModal__Content--after-open {
	opacity: 1;
	-webkit-transform: translateY(0) perspective(600px) rotateX(0) scale(1);
	        transform: translateY(0) perspective(600px) rotateX(0) scale(1);
	-webkit-transition: 0.15s opacity, 0.15s -webkit-transform;
	transition: 0.15s opacity, 0.15s -webkit-transform;
	transition: 0.15s transform, 0.15s opacity;
	transition: 0.15s transform, 0.15s opacity, 0.15s -webkit-transform;
}

.ReactModal__Content--before-close {
	opacity: 0;
	-webkit-transform: translateY(10px) perspective(600px) rotateX(0) scale(1);
	        transform: translateY(10px) perspective(600px) rotateX(0) scale(1);
}

.ReactModal__FitContent > :first-child,
.ReactModal__FitContent > :first-child > :first-child {
	margin-top: 0;
}

.Form {
	margin: 0;
}

.Form__buttons {
	margin-top: 1ex;
	text-align: end;
}

.Form__buttons button {
	margin-left: 1ex;
}

.Field {
	margin-bottom: 1ex;
}

.Field input[type="checkbox"],
.Field input[type="radio"] {
	vertical-align: -1px;
}

.Field input[type="text"] {
	box-sizing: border-box;
	width: 100%;
}

.Field__label {
	color: #333;
	display: block;
	font-weight: bold;
}

.BreakpointModal {
	min-width: 40ex;
}

.DisasmSearch {
	background: #3999bd;
	padding: 7px;
	position: absolute;
	top: 25px;
	right: 20px;
}

.DisasmSearch__field {
	display: inline-block;
	font-size: 13px;
	overflow: hidden;
	position: relative;
	width: 24ex;
	max-width: 80%;
	vertical-align: top;
}

.DisasmSearch input[type="search"] {
	background: #fff;
	border: 0;
	box-sizing: border-box;
	font-size: inherit;
	padding: 2px 6px;
	width: 100%;
}

.DisasmSearch__field--progress::after {
	-webkit-animation: 2s DisasmSearch__progress ease-in-out infinite;
	        animation: 2s DisasmSearch__progress ease-in-out infinite;
	background: #f88;
	content: '';
	display: block;
	position: absolute;
	left: 0;
	bottom: 0;
	height: 2px;
	width: 30%;
}

@-webkit-keyframes DisasmSearch__progress {
	from {
		left: -30%;
	}

	to {
		left: 100%;
	}
}

@keyframes DisasmSearch__progress {
	from {
		left: -30%;
	}

	to {
		left: 100%;
	}
}

.DisasmSearch button {
	border: 0;
	background: transparent;
	padding: 2px;
	margin-left: 5px;
	vertical-align: top;
}

.DisasmSearch button svg {
	fill: transparent;
	stroke: #000;
	stroke-width: 16px;
	stroke-linecap: round;
	stroke-linejoin: miter;
	vertical-align: top;
}

.DisasmSearch button:hover,
.DisasmSearch button:active,
.DisasmSearch button:focus {
	background: rgba(255, 255, 255, 0.2);
}

.StatusBar {
	background: #eee;
	border-top: 1px solid #ccc;
	border-bottom-left-radius: 1ex;
	border-bottom-right-radius: 1ex;
	display: flex;
	line-height: 1.5em;
	height: 1.5em;
	overflow: hidden;
	padding: 1px 1ex;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.StatusBar__left {
	flex: 1 1 auto;
}

.StatusBar__right {
	text-align: right;
}

.Disasm {
	font-family: Consolas, monospace;
	font-size: 14px;
	min-height: 10em;
	max-height: calc(100% - 25px);
	line-height: 1.26;
	overflow-y: auto;
}

.Disasm--not-started {
	align-items: center;
	display: flex;
	flex: 1 1 auto;
	font-size: larger;
	justify-content: center;
}

.Disasm code {
	font-family: Consolas, monospace;
}

.Disasm__container {
	display: flex;
	flex-direction: column;
	position: relative;
	width: 100%;
}

.Disasm__list {
	position: relative;
	text-align: start;
	/* We handle selection. */
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.Disasm .react-contextmenu-wrapper:focus {
	/* Already shown on selection. */
	outline: none;
}

.DisasmLine {
	white-space: nowrap;
}

.DisasmLine__breakpoint-icon {
	padding-left: 2px;
	fill: transparent;
}

.DisasmLine--current {
	/* This uses a linear-gradient to lighten the js-specified background-color value without affecting the text. */
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.5)), to(rgba(255, 255, 255, 0.5)));
	background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5));
	background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5));
}

.DisasmLine--selected {
	background: -webkit-gradient(linear, left top, left bottom, from(#bcd), to(#bcd));
	background: -webkit-linear-gradient(top, #bcd, #bcd);
	background: linear-gradient(to bottom, #bcd, #bcd);
}

.DisasmLine--focused {
	background: -webkit-gradient(linear, left top, left bottom, from(#39f), to(#39f));
	background: -webkit-linear-gradient(top, #39f, #39f);
	background: linear-gradient(to bottom, #39f, #39f);
	color: #fff;
}

.DisasmLine--focused.DisasmLine--cursor {
	background: -webkit-gradient(linear, left top, left bottom, from(#28f), to(#28f));
	background: -webkit-linear-gradient(top, #28f, #28f);
	background: linear-gradient(to bottom, #28f, #28f);
}

.DisasmLine--breakpoint {
	color: #f00;
}

.DisasmLine--breakpoint .DisasmLine__breakpoint-icon {
	fill: #f00;
}

.DisasmLine--disabled-breakpoint .DisasmLine__breakpoint-icon {
	fill: #999;
}

.DisasmLine--focused.DisasmLine--breakpoint {
	text-shadow: 0 0 2px rgba(255, 255, 255, 0.8);
}

.DisasmLine--focused.DisasmLine--breakpoint .DisasmLine__breakpoint-icon {
	-webkit-filter: drop-shadow(0 0 1px rgba(255, 255, 255, 0.8));
	        filter: drop-shadow(0 0 1px rgba(255, 255, 255, 0.8));
}

.DisasmLine--focused.DisasmLine--disabled-breakpoint .DisasmLine__breakpoint-icon {
	fill: #bbb;
}

.DisasmLine__address {
	display: inline-block;
	overflow: hidden;
	text-overflow: ellipsis;
	vertical-align: top;
	width: 20ex;
}

.DisasmLine__address--nosymbol {
	text-align: center;
}

.DisasmLine__opcode {
	display: inline-block;
	font-weight: bold;
	vertical-align: top;
	width: 12ex;
}

.DisasmLine__opcode::before {
	content: "";
	display: inline-block;
	font-size: 12px;
	position: relative;
	left: -2px;
	width: 1ex;
}

.DisasmLine--current .DisasmLine__opcode::before {
	content: "\25a0";
}

.DisasmLine__params {
	display: inline-block;
	position: relative;
	text-overflow: ellipsis;
	vertical-align: top;
}

.DisasmLine__param--highlighted {
	color: #0ba;
}

.DisasmLine__highlight {
	display: inline-block;
	position: relative;
	z-index: 0;
}

.DisasmLine__highlight--end {
	width: 100%;
}

.DisasmLine__highlight::before {
	display: inline-block;
	content: '';
	background: #8cf;
	border-radius: 1ex;
	position: absolute;
	top: -1px;
	left: -1px;
	right: -1px;
	bottom: -1px;
	z-index: -1;
}

.DisasmLine--focused .DisasmLine__highlight::before {
	background: #4af;
}

.DisasmBranchGuide {
	position: absolute;
	width: 8px;
}

.DisasmBranchGuide path {
	fill: none;
	stroke: #23f;
	stroke-width: 1px;
}

.DisasmBranchGuide--selected path {
	stroke: #fa7a25;
}

.DisasmButtons {
	/* Matches with the Diasm max-height. */
	height: 25px;
}

.DisasmButtons__spacer {
	display: inline-block;
	width: 12px;
}

.DisasmButtons button {
	margin-right: 3px;
}

.DisasmButtons__thread--current {
	font-weight: bold;
}

.GotoBox {
	flex: 0 0 auto;
	padding: 5px;
	padding-top: 2px;
}

.GotoBox__label {
	display: block;
	padding-bottom: 3px;
}

#GotoBox__address {
	width: 11ex;
	margin-right: 4px;
}

.GotoBox__button {
	background: transparent;
	border: 0;
	font-size: inherit;
	padding: 0 3px;
}

.GotoBox__button:enabled {
	cursor: pointer;
}

.GotoBox__button:enabled:active,
.GotoBox__button:enabled:hover,
