#RegPanel {
	display: flex;
	flex: 1 1 auto;
	flex-direction: column;
	width: 22ex;
}

#RegPanel dl {
	font-family: Consolas, monospace;
	line-height: 0.8em;
	margin: 0;
	margin-top: 1px;
	height: calc(28em + 71px);
	overflow-y: auto;
	text-align: start;
}

#RegPanel dt {
	clear: both;
	cursor: default;
	float: left;
	margin: 0;
	margin-top: 1px;
	margin-bottom: 1px;
	padding: 0;
	width: 8ex;
}

#RegPanel dt::before {
	background: #e8efff;
	content: "\00A0";
	display: block;
	float: left;
	margin-left: 1px;
	margin-right: 2px;
	width: 14px;
}

#RegPanel dd {
	border: 1px solid transparent;
	cursor: default;
	margin: 0;
	margin-left: 16px;
	padding: 0;
	padding-left: 8ex;
}

#RegPanel dd:active {
	border-color: #666;
}

.RegPanel__item--selected {
	background: #e0e8ff;
	background-clip: padding-box;
}

.RegPanel__item--changed {
	color: red;
}

#RegPanel .react-contextmenu-wrapper:focus {
	/* Already shown on selection. */
	outline: none;
}

#RegPanel .react-tabs {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
}

#RegPanel .react-tabs__tab-panel--selected {
	display: flex;
	flex: 1 1 auto;
	flex-direction: column;
}

/* These fix a bug in IE11. */
.LeftPanel .react-tabs__tab-list {
	flex: 0 0 auto;
}

.LeftPanel,
.LeftPanel .react-tabs,
.LeftPanel .react-tabs__tab-panel,
#RegPanel {
	height: 100%;
}

#RegPanel .react-tabs__tab-panel {
	height: calc(100% - 22px);
}

.react-contextmenu {
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: .25rem;
    color: #373a3c;
    font-size: 13px;
    margin: 2px 0 0;
    min-width: 160px;
    outline: none;
    opacity: 0;
    padding: 3px 0;
    text-align: left;
    -webkit-transition: opacity 0.1s ease-out;
    transition: opacity 0.1s ease-out;
}

.react-contextmenu.react-contextmenu--visible {
    opacity: 1;
    pointer-events: auto;
    z-index: 9999;
}

.react-contextmenu-item {
    background: 0 0;
    border: 0;
    color: #373a3c;
	cursor: pointer;
    font-weight: 400;
    line-height: 1.4;
    padding: 3px 16px;
    text-align: inherit;
    white-space: nowrap;
}

.react-contextmenu-item--active,
.react-contextmenu-item--selected {
    color: #fff;
    background-color: #28f;
    text-decoration: none;
}

.react-contextmenu-item--disabled,
.react-contextmenu-item--disabled:hover {
    background-color: transparent;
    color: #888;
}

.react-contextmenu-item--divider {
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    cursor: inherit;
    margin-bottom: 3px;
    padding: 2px 0;
}

.react-contextmenu-submenu {
	padding: 0;
}

.react-contextmenu-submenu > .react-contextmenu-item:after {
	content: "\25B6";
	display: inline-block;
	position: absolute;
	right: 7px;
}

.react-tabs__tab-list {
	border-bottom: 2px solid #3999bd;
	margin: 0;
	padding: 0;
	text-align: start;
}

.react-tabs__tab {
	display: inline-block;
	border: 1px solid transparent;
	border-bottom: none;
	bottom: -1px;
	position: relative;
	list-style: none;
	padding: 3px 6px;
	cursor: pointer;
}

.react-tabs__tab--selected {
	background: #3999bd;
	color: #fff;
}

.react-tabs__tab--disabled {
	color: GrayText;
	cursor: default;
}

.react-tabs__tab:focus {
	background-color: #4cc2ed;
	outline: none;
}

.react-tabs__tab-panel {
	display: none;
}

.react-tabs__tab-panel--selected {
	display: block;
}

/* Nested tabs... */
.react-tabs__tab-panel .react-tabs__tab-list {
	background: #3999bd;
	border-bottom: 2px solid #fff;
	color: #fff;
}

.react-tabs__tab-panel .react-tabs__tab--selected {
	background: #fff;
	color: #3999bd;
}

